body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
  background: #003973;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #E5E5BE, #003973); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid{
  background: #003973;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #E5E5BE, #003973); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+,  Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.avatar-img{
  height: 250px;
  margin-top: 100px;
  border-radius: 25px 25px 25px 25px;
}
.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}
.social-links{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 20%;
  margin: auto;
}
.social-links i{
  color:white;
  font-size: 5em;
  padding: 8px;
}
.projects-grid{
  display: -webkit-flex;
  display: flex;
}
.contact-body{
  margin:auto;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2em;
}
.contact-grid{
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}
.contact-grid h2{
  font-family: 'Anton', sans-serif;
}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list i{
  font-size: 66px;
  padding-right: 5rem;
}
.resume-right-col{
  background: #737673;
  color: white;
  padding: 2em;
}

@media all and (max-width: 320px) {
  .banner-text h1{
    font-size: 30px;
    font-weight: bold;
    color: white;
  }
  .avatar-img{
    height: 200px;
    margin-top: 30px;
    border-radius: 10px 10px 10px 10px;
  }
  .social-links i{
    color:white;
    font-size: 3em;
    padding: 8px;
  }
  .banner-text {
    background-color: black;
    opacity: .8;
    width: 95%;
    margin: auto;
    border-radius: 10px;
  }
  .banner-text p{
    color: white;
    font-size: 20px;
    padding: 0em;
  }
  .projects-grid{
    display: inline;
  }
  .category-tabs{
    margin-top: 50px;
    width: 100%;
  }
  .project-card{
    margin-bottom: 20px;
  }
}
@media all and (max-width: 375px) and (min-width: 325px) {
  .avatar-img{
    margin-top: 20px;
    height: 175px;
  }
  .banner-text h1{
    font-size: 30px;
    font-weight: bold;
    color: white;
  }
  .banner-text p{
    color: white;
    font-size: 20px;
    padding: 0em;
    margin-top: 10px;
  }
  .social-links i{
    color:white;
    font-size: 2em;
    padding: 8px;
  }
  .projects-grid{
    display: inline;
  }
  .category-tabs{
    margin-top: 50px;
    width: 100%;
  }
  .project-card{
    margin-bottom: 20px;
  }
}
@media all and (max-width: 425px) and (min-width: 376px) {
  .avatar-img{
    margin-top: 20px;
    height: 175px;
  }
  .banner-text h1{
    font-size: 30px;
    font-weight: bold;
    color: white;
  }
  .banner-text p{
    color: white;
    font-size: 20px;
    padding: 0em;
    margin-top: 10px;
  }
  .social-links i{
    color:white;
    font-size: 2em;
    padding: 8px;
  }
  .projects-grid{
    display: inline;
  }
  .category-tabs{
    margin-top: 50px;
    width: 100%;
  }
  .project-card{
    margin-bottom: 20px;
  }
}
@media all and (max-width: 768px) and (min-width: 426px) {
  .banner-text h1{
    font-size: 50px;
    font-weight: bold;
    color: white;
  }
  .avatar-img{
    height: 250px;
    margin-top: 200px;
    margin-bottom: 200px;
    border-radius: 25px 25px 25px 25px;
  }
  .projects-grid{
    display: inline;
    margin: auto;
  }
  .banner-text {
    background-color: black;
    opacity: .8;
    width: 95%;
    margin: auto;
    border-radius: 10px;
  }
  .social-links i{
    color:white;
    font-size: 5em;
    padding: 8px;
  }
  .category-tabs{
    margin-top: 40px;
  }
  .project-card{
    margin-bottom: 20px;
  }
}
